<template>
  <div class="detail" v-if="detail">
    <van-swipe>
      <van-swipe-item
        v-for="(image, index) in detail.storeInfo.slider_image"
        :key="index"
      >
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>
    <div class="details">
      <p class="details1">
        <span>￥{{ detail.storeInfo.price }}</span>
        <span>￥{{ detail.storeInfo.vip_price }}</span>
      </p>
      <p class="details2">{{ detail.storeInfo.store_name }}</p>
      <p class="details3">
        <span>原价:￥{{ detail.storeInfo.ot_price }}</span>
        <span>库存:{{ detail.storeInfo.stock }}</span>
        <span
          >销量:{{ detail.storeInfo.fsales }}
          {{ detail.storeInfo.unit_name }}</span
        >
      </p>
    </div>
    <van-cell-group class="titlert">
      <van-cell center is-link>
        <template #title>
          <div>
            <span class="custom-title titleson">已选择:</span>
            <span class="custom-title titleson2"
              >{{ skuKeyStrs1 }}{{ skuKeyStrs2 }}</span
            >
          </div>
        </template>
        <template #default>
          <div class="titleson3">
            <span class="more"></span>
          </div>
        </template>
      </van-cell>
    </van-cell-group>
    <van-cell-group class="titlert">
      <van-cell center is-link>
        <template #title>
          <div class="titlertes">
            <span class="custom-title titleson">用户评价(0)</span>
            <span class="custom-title titleson2"
              ><span>0%</span><span>好评率</span></span
            >
          </div>
        </template>
        <template #default>
          <div class="titleson3">
            <span class="more"></span>
          </div>
        </template>
      </van-cell>
    </van-cell-group>
    <div class="title1">
      <img src="../assets/youpin.png" alt="" />
      <div>优品推荐</div>
      <img src="../assets/youpin.png" alt="" />
    </div>
    <premiumes :bargoodlist="bargoodlist"></premiumes>
    <div class="title1">
      <div class="title1son">产品介绍</div>
    </div>
    <div class="box1" v-html="goods.description"></div>
    <van-goods-action>
      <van-goods-action-icon to="/cart" icon="cart-o" text="购物车" />
      <van-goods-action-button
        type="warning"
        text="加入购物车"
        @click="Shopping"
      />
      <van-goods-action-button
        type="danger"
        text="立即购买"
        @click="Shopping"
      />
    </van-goods-action>
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      @buy-clicked="buyclicked"
      @add-cart="addCart"
      :initial-sku="initialSku"
      @stepper-change="stepperchange"
      @sku-selected="skuSelected"
    />
  </div>
</template>
<script>
import { mapMutations } from "vuex";
import Premiumes from "@/components/Premiumes";
export default {
  name: "Detail",
  props: ["id"],
  data() {
    return {
      detail: null,
      show: false,
      skuKeyStrs1: "",
      skuKeyStrs2: "",
      bargoodlist: [], //优品推荐
      sku: {
        tree: [
          {
            k: "颜色",
            k_id: "1",
            v: [
              {
                id: "30349",
                name: "红色",
                imgUrl: "http://b.yzcdn.cn/vant/sku/shoes-1.png",
              },
              {
                id: "1215",
                name: "白色",
                imgUrl: "http://b.yzcdn.cn/vant/sku/shoes-1.png",
              },
            ],
            k_s: "s1",
          },
          {
            k: "尺寸",
            k_id: "2",
            v: [
              {
                id: "1193",
                name: "30码",
              },
              {
                id: "1194",
                name: "40码",
              },
            ],
            k_s: "s2",
            count: 2,
          },
        ],
        list: [
          {
            id: 2259,
            price: 120,
            s1: "1215",
            s2: "1193",
            stock_num: 20,
            good_id: 946755,
          },
          {
            id: 2260,
            price: 110,
            s1: "1215",
            s2: "1194",
            stock_num: 2,
            good_id: 946755,
          },
          {
            id: 2257,
            price: 130,
            s1: "30349",
            s2: "1193",
            stock_num: 40,
            good_id: 946755,
          },
          {
            id: 2258,
            price: 100,
            s1: "30349",
            s2: "1194",
            stock_num: 50,
            good_id: 946755,
          },
        ],
        price: "5.00",
        stock_num: 227, //商品总库存
        none_sku: false, //是否无规格商品
        hide_stock: false, //是否隐藏剩余库存
      },
      initialSku: {
        s1: "1215",
        s2: "1193",
      },
      goods: {},
      goodsId: 1,
      Purchasequantity: 1, //购买数量
      selectedSkuComb: "", //商品规格ID
    };
  },
  components: {
    Premiumes,
  },
  created() {
    this.changeLoading(true);
    this.axios.get("/api/product/detail/" + this.id).then((d) => {
      function group(array, subGroupLength) {
        let index = 0;
        let newArray = [];
        while (index < array.length) {
          newArray.push(array.slice(index, (index += subGroupLength)));
        }
        return newArray;
      }
      var groupedArray = group(d.data.data.good_list, 6);
      this.bargoodlist = groupedArray;
      let arrs = d.data.data.productAttr;
      let listValue = d.data.data.productValue;
      this.goodsId = this.id;
      this.goods = d.data.data.storeInfo;
      //sku 数据转换
      let id = 1;
      let idobj = {};
      let ssk = 1;
      this.sku.list = [];
      if (listValue.length != 0) {
        this.goods.picture = listValue[Object.keys(listValue)[0]].image;
      }
      this.sku.tree = arrs.map((atr, ads) => {
        let va = atr.attr_value.map((at, index) => {
          id++;
          idobj[at.attr] = id;
          let attrs = {
            id: id,
            name: at.attr,
          };
          if (ads < 1) {
            attrs.imgUrl = listValue[Object.keys(listValue)[index]].image;
          }
          return attrs;
        });
        var ks = "s" + ssk;
        ssk++;
        return {
          k: atr.attr_name,
          k_id: atr.attr_name,
          v: va,
          k_s: ks,
        };
      });
      //循环产品属性组合,组装sku列表
      for (let key in listValue) {
        let v = listValue[key];
        let keys = key.split(",");
        this.sku.list.push({
          id: v.unique,
          price: v.price,
          s1: idobj[keys[0]],
          s2: idobj[keys[1]],
          stock_num: v.stock, //库存
          good_id: v.product_id,
          imgUrl: v.image,
        });
      }
      this.detail = d.data.data;
      console.log(this.sku.list);
      this.changeLoading(false);
    });
  },
  methods: {
    //购买
    buyclicked() {
      this.$axios
        .post("/api/cart/add", {
          cartNum: this.Purchasequantity,
          new: 0,
          productId: this.id,
          uniqueId: this.selectedSkuComb,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$router.push({
              path: "/payment",
              query: { id: d.data.data.cartId },
            });
            console.log(d.data.data.cartId)
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    //购物车显示
    Shopping() {
      this.show = true;
    },
    //判断sku是否有商品规格设置
    skuSelected(skuValue) {
      if (skuValue.selectedSkuComb != null) {
        this.selectedSkuComb = skuValue.selectedSkuComb.id;
      }
      console.log(skuValue);
      if (skuValue.skuValue.skuKeyStr == "s1") {
        this.skuKeyStrs1 = skuValue.skuValue.name;
      } else if (skuValue.skuValue.skuKeyStr == "s2") {
        this.skuKeyStrs2 = skuValue.skuValue.name;
      }
    },
    stepperchange(event) {
      console.log(event);
      //将当前购买数量赋值Purchasequantity
      this.Purchasequantity = event;
    },
    //购物车post
    addCart() {
      this.show = false;
      this.$axios
        .post("/api/cart/add", {
          cartNum: this.Purchasequantity,
          new: 0,
          productId: this.id,
          uniqueId: this.selectedSkuComb,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast({
              message: "添加购物车成功",
            });
            console.log(this.id, this.Purchasequantity, d.data.data.cartId);
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    ...mapMutations(["changeLoading"]),
  },
};
</script>
<style lang="less">
.detail {
  background-color: white;
  .van-swipe {
    height: 375px;
    .van-swipe__track {
      .van-swipe-item {
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .details {
    p {
      margin: 0 15px;
    }
    .details1 {
      padding-top: 12.5px;
      span {
        &:first-child {
          color: #fc4141;
          font-weight: 700;
          font-size: 24px;
        }
        &:nth-child(2) {
          font-weight: 700;
          font-size: 14px;
        }
      }
    }
    .details2 {
      margin-top: 5px;
      font-weight: 700;
      font-size: 16px;
    }
    .details3 {
      margin-top: 11px;
      padding-bottom: 12.5px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        color: #82848f;
        font-size: 12px;
      }
    }
  }
  .titlert {
    background-color: transparent;
    .van-cell-group {
      background-color: transparent;
    }
    .van-cell {
      background-color: transparent;
    }
    .van-cell__right-icon {
    }
    .van-cell__title {
      -webkit-flex: 7;
      .titleson {
        color: #82848f;
        font-size: 13px;
        margin-right: 10px;
      }
      .titleson2 {
        font-size: 14px;
      }
    }
    .titlertes {
      width: 115%;
      display: flex;
      justify-content: space-between;
      .titleson {
        color: #82848f;
        font-size: 13px;
        margin-right: 10px;
      }
      .titleson2 {
        font-size: 14px;
        span {
          color: grey;
          &:first-child {
            color: #fc4141;
          }
        }
      }
    }
  }
  .Premiumes {
    .Premiumesimg {
      width: 107.5px;
      height: 107.5px;
    }
    p {
      margin: 0;
    }
    .p1 {
      width: 107.5px;
      font-size: 13px;
      margin-top: 6px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .p2 {
      width: 107.5px;
      font-size: 13px;
      color: #fc4141;
    }
  }
  .title1 {
    line-height: 30px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    div {
      color: #f21b07;
      font-size: 15px;
    }
    img {
      width: 15px;
      height: 15px;
    }
    .title1son {
      color: #000;
    }
  }
  .box1{
    width: 100%;
    height: auto;
    font-size: 12px !important;
    div{
      width: 375px !important;
    }
    p{
      width: 375px;
    }
    img{
      width: 375px;
      display: block;
    }
  }
}
</style>